*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}

body,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

main {
  display: block;
}

span {
  display: inline-block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1rem;
}

a {
  display: inline-block;
  background-color: transparent;
  text-decoration: none;
  color: black;
}

ul,
ol {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1rem;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img,
textarea,
svg,
object,
::after,
::before,
a {
  border-style: none;
  display: inline-block;
  vertical-align: bottom;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
  border: none;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35rem 0.75rem 0.625rem;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details,
table {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

#preloader {
  width: 100%;
  height: 100%;
}

#loader {
  display: block;
  position: relative;
  left: 50%;
  top: 50%;
  width: 150px;
  height: 150px;
  margin: -75px 0 0 -75px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #9370DB;
  animation: spin 2s linear infinite;
}

#loader::before,
#loader::after {
  content: "";
  position: absolute;
  border: 3px solid transparent;
  border-radius: 50%;
}

#loader::before {
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border-top-color: #BA55D3;
  animation: spin 3s linear infinite;
}

#loader::after {
  top: 15px;
  left: 15px;
  right: 15px;
  bottom: 15px;
  border-top-color: #FF00FF;
  animation: spin 1.5s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

:root {
  font-size: 10px;
}

.mail-popup__form {
  display: flex;
  flex-wrap: wrap;
}

body {
  background-color: #181818;
  font-family: 'MyriadPro-Regular', serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.title-two {
  margin: 4.7rem auto 0;
  font-size: 2.8rem;
  text-transform: uppercase;
  color: #868686;
  text-align: center;
}

.title-two__end {
  color: #f96763;
}

.title-two::after {
  content: '';
  width: 33.7rem;
  height: .1rem;
  background-color: #adadad;
  display: block;
  margin: .8rem auto 0;
}

.subtitle {
  max-width: 52.8rem;
  text-align: center;
  font-size: 2.0rem;
  line-height: 2.5rem;
  color: #adadad;
  margin: 2rem auto 5.4rem;
}

.next-slider-btn {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  display: block;
  margin: 2rem auto 7.2rem;
  background-color: #fc635e;
  border: none;
  outline: none;
  box-shadow: 0 0 10px 3px #b5b6b3;
}

.next-slider-btn::before,
.next-slider-btn::after {
  content: '';
  display: block;
  margin: 0 auto;
  border-width: .2rem;
  border-style: solid;
  border-left: none;
  border-top: none;
  border-color: #fff;
}

.next-slider-btn::before {
  width: 0;
  height: 2rem;
  border-bottom: none;
  transform: translateY(0.5rem);
}

.next-slider-btn::after {
  width: 1rem;
  height: 1rem;
  background-color: transparent;
  transform: translateY(-0.5rem) rotate(45deg);
}

.next-slider-btn--bg-off {
  background-color: transparent;
  border: 1px solid #fc635e;
}

.next-slider-btn--bg-off::after,
.next-slider-btn--bg-off::before {
  border-color: #fc635e;
}

.next-slider-btn:hover {
  cursor: pointer;
  background-color: #fff;
}

.description {
  padding: 2.8rem 5.1rem;
}

.description__title {
  font-size: 2.8rem;
  line-height: 4rem;
  color: #2d2c2c;
}

.description__title::before,
.description__title::after {
  content: '';
  display: inline-block;
  width: 1rem;
  height: 4rem;
  margin-right: 1.7rem;
  background-color: #f96763;
}

.description__title::after {
  display: none;
}

.description__text {
  font-size: 1.6rem;
  color: #9e9898;
  line-height: 2.5rem;
}

.description--right {
  text-align: right;
}

.description--right .description__title::before {
  display: none;
}

.description--right .description__title::after {
  display: inline-block;
  margin: 0 0 0 1.7rem;
}

::-webkit-scrollbar {
  width: 11px;
}

::-webkit-scrollbar-button {
  width: 6px;
  height: 2px;
}

::-webkit-scrollbar-track {
  background-color: #32312e;
  border-radius: 5px;
  box-shadow: 0px 0px 3px #000 inset;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #7e7e7eb0;
  box-shadow: 0px 1px 1px #8b8b8b inset;
}

::-webkit-resizer {
  width: 7px;
  height: 0px;
}

.popup {
  position: fixed;
  z-index: 3;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
  justify-content: center;
  align-items: center;
  background: rgba(0,0,0,0.6);
  backdrop-filter: blur(10px);
}

.popup__content {
  flex-basis: 100%;
  max-width: 130rem;
  color: #dc143c;
  background: rgba(22,21,21,0.8);
  padding: 2rem;
  font-size: 2em;
  border-radius: .5rem;
  box-shadow: 0 0 4px 1px black;
  position: relative;
  animation: slide-in-top 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.popup__title {
  text-align: center;
  padding: 0 0 1rem;
}

.popup__close {
  position: absolute;
  width: 3.2rem;
  height: 3.2rem;
  right: 2rem;
  top: 1.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.popup__close::before,
.popup__close::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 13%;
  background-color: #d6d6d6;
  transition: all linear .1s;
}

.popup__close::before {
  transform: rotate(45deg);
}

.popup__close::after {
  transform: rotate(-45deg);
}

.popup__close:hover::before,
.popup__close:hover::after {
  width: 110%;
  height: 17%;
  background-color: crimson;
}

@keyframes slide-in-top {
  0% {
    transform: translateY(-1000px);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.header {
  height: 8rem;
  position: fixed;
  width: 100%;
  z-index: 5;
}

.header__items {
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #202127;
  animation: headerBorder 8s ease alternate infinite;
}

.header__phone {
  font-family: "Consolas", serif;
  font-size: 2rem;
  color: #cfcdcd;
  transition: all 1s linear;
  position: relative;
  padding: 10px;
  text-shadow: 0 0 10px rgba(36,34,34,0.938),0 0 30px rgba(255,255,255,0.45),0 0 100px rgba(255,255,255,0.1);
}

.header__phone::before,
.header__phone::after {
  content: '';
  position: absolute;
  transition: transform .5s ease;
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-style: solid;
  border-radius: .4rem;
  border-color: rgba(11,170,212,0.8);
}

.header__phone::before {
  border-width: 2px 0 2px 0;
  transform: scaleX(0);
}

.header__phone::after {
  border-width: 0 2px 0 2px;
  transform: scaleY(0);
}

.header__phone:hover::before,
.header__phone:hover::after {
  transform: scale(1, 1);
}

.social {
  display: flex;
}

.social__link {
  display: flex;
  align-items: center;
}

.social__link-name {
  font-size: 2rem;
  font-family: 'GothamPro-Bold', serif;
  opacity: 0;
  width: 11.5rem;
  text-align: center;
  filter: blur(12px);
  letter-spacing: -0.5rem;
  transition: all .3s linear;
}

.social__link-name::after {
  content: '';
  width: 100%;
  height: 2px;
  display: block;
  transform: translateY(2px);
  background: linear-gradient(90deg, #303770, #3fc640);
}

.social__link-name--whatsapp {
  order: -1;
  background: linear-gradient(20deg, #303770 20%, #3fc640);
}

.social__link-name--instagram {
  order: 1;
  background: linear-gradient(20deg, #b52ca1 10%, #fed655);
}

.social__link-name--instagram::after {
  background: linear-gradient(90deg, #b52ca1, #fed655);
}

.social__link-name--whatsapp,
.social__link-name--instagram {
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.svg-whatsapp,
.svg-instagram {
  max-width: 8rem;
  max-height: 8rem;
  outline: none;
  filter: drop-shadow(0 0 10px rgba(0,0,0,0.7));
}

.svg-whatsapp__no-active,
.svg-whatsapp__active,
.svg-instagram__no-active,
.svg-instagram__active {
  transform-origin: 50% 50%;
}

.svg-whatsapp__no-active,
.svg-instagram__no-active {
  fill: #7a7e97;
}

.svg-whatsapp__active,
.svg-instagram__active {
  opacity: 0;
  transition: all .2s ease-in;
}

.svg-whatsapp:hover .svg-whatsapp__no-active,
.svg-whatsapp:hover .svg-whatsapp__active,
.svg-instagram:hover .svg-whatsapp__no-active,
.svg-instagram:hover .svg-whatsapp__active,
.svg-whatsapp:hover .svg-instagram__no-active,
.svg-whatsapp:hover .svg-instagram__active,
.svg-instagram:hover .svg-instagram__no-active,
.svg-instagram:hover .svg-instagram__active {
  animation: bounce-top .9s both;
  opacity: 1;
}

.svg-whatsapp:hover ~ .social__link-name,
.svg-instagram:hover ~ .social__link-name {
  opacity: 1;
  letter-spacing: normal;
  filter: blur(0px);
}

.price-btn {
  position: relative;
  width: 16rem;
  height: 4.4rem;
  border-radius: 0.7rem;
  background-color: #8388A4;
  color: #fff;
  font-size: 2rem;
  overflow: hidden;
  outline: none;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23);
  transition: all .1s ease-in-out;
}

.price-btn::before,
.price-btn::after {
  content: '';
  position: absolute;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all .2s ease-in-out;
}

.price-btn::before {
  top: -125%;
  left: -10%;
  width: 120%;
  transform-origin: 20% 50%;
  transform: rotateZ(-20deg);
  background-color: rgba(26,188,156,0.8);
}

.price-btn::after {
  content: attr(data-name);
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.price-btn:hover::before {
  top: 0%;
  transform: rotateZ(0deg);
}

.price-btn:hover::after {
  color: #202127;
}

.price-btn:hover {
  transform: scale(1.03);
}

@keyframes headerBorder {
  0% {
    border-bottom: 5px solid #6c67cf;
  }

  30% {
    border-bottom: 5px solid #CC39A1;
  }

  65% {
    border-bottom: 5px solid #6c67cf;
  }

  100% {
    border-bottom: 5px solid #4b84c8;
  }
}

@keyframes bounce-top {
  0% {
    transform: translateY(0px) scale3d(1, 1, 1);
    animation-timing-function: ease-in;
  }

  10% {
    transform: translateY(-18px);
  }

  30% {
    transform: scale3d(1.15, 0.85, 1);
  }

  40% {
    transform: translateY(-14px) scale3d(0.9, 1.1, 1);
    animation-timing-function: ease-in;
  }

  50% {
    transform: scale3d(1.1, 0.9, 1);
  }

  65% {
    transform: translateY(-12px) scale3d(0.95, 1.05, 1);
    animation-timing-function: ease-in;
  }

  75% {
    transform: scale3d(1.05, 0.95, 1);
  }

  82% {
    transform: translateY(-6px);
    animation-timing-function: ease-in;
  }

  93% {
    transform: translateY(-4px);
    animation-timing-function: ease-in;
  }

  25%, 55%, 75%, 87% {
    transform: translateY(0px);
    animation-timing-function: ease-out;
  }

  100% {
    transform: translateY(0px) scale3d(1, 1, 1);
    animation-timing-function: ease-out;
  }
}

.price-list__table {
  position: relative;
  height: 60rem;
  overflow: auto;
  border-radius: .5rem;
}

.price-list__table-body {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(22,21,21,0.95);
  padding: 0 1rem;
}

.price-list__cell-head-title {
  padding: 1rem 0;
}

.price-list__cell-info {
  width: 100%;
  text-align: center;
  background: rgba(0,0,0,0.8);
  font-weight: bold;
  letter-spacing: .1rem;
}

.price-list__cell-info,
.price-list__cell {
  word-wrap: break-word;
  padding: 1rem;
}

.price-list__cell-info:nth-child(1),
.price-list__cell:nth-child(1) {
  border-bottom: 2px solid #000;
  border-left: 2px solid #000;
}

.price-list__cell-info+.price-list__cell-info,
.price-list__cell+.price-list__cell-info,
.price-list__cell-info+.price-list__cell,
.price-list__cell+.price-list__cell {
  white-space: nowrap;
  background: rgba(0,0,0,0.8);
